.styleCardTitle {
  font-family: Avenir-Heavy;
  font-size: 20;
  font-weight: 600;
  margin: 12px 0px 0px 0px;
}

.styleImage {
  background-color: lightGray;
  border-radius: 8px 8px 0 0;
  max-height: 220px;
  overflow: hidden;
}

.styleLocationLabel {
  color: #8294aa;
  font-size: 14;
  margin: 4px 0;
}

.styleDescription {
  font-size: 14;
  margin: 8px 0 0 0;
  word-wrap: break-word;
}
.styleCard {
  border-radius: 8px;
  box-shadow: 0px 0px 8px #ccc;
  color: #253b56;
  margin: 12px 0px;
  transition: margin 0.2s ease-in-out;
  position: relative;
  box-sizing: border-box;
}

.styleCardContent {
  padding: 4px 16px 20px 16px;
}

.comment-block {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
  flex-direction: row;
}
.trash {
  position: absolute;
  right: 0px;
  color: red;
}
.trash:hover {
  cursor: pointer;
}