.myOrdersPage{
    width : 100vw;
    max-width : 100%;
    padding : 0 7vmax;
    box-sizing : border-box;
    background-color: rgb(243, 242, 242);
    position: fixed;
    display: flex;
    height : 93vh;
    top : 100;
    left : 0;
    flex-direction: column;
}

.myOrdersHeading {
    text-align: center;
    font : 400 1.2vmax "Roboto";
    padding : 0.5vmax;
    box-sizing : border-box;
    color : white;
    transition : all 0.5s;
    background-color : black;
}
.myOrdersTable {
    background-color: white;
}

.myOrdersTable  div {
    font : 300 1vmax "Roboto";
    color : rgba(0,0,0,0.678);
    border : none;
}

.myOrdersTable a:hover {
    color : rgb(149, 180, 199);
}

.MuiDataGrid-columnHeader {
    background-color : rgb(184, 202, 224);
    padding : 1vmax !important;
}
.MuiDataGrid-columnHeader div {
    color : white;
    font : 500 1.1vmax "Roboto" !important;
}
.MuiDataGrid-iconSeparator {
    display : none !important;
}

@media screen and (max-width:600px){
    .myOrdersPage {
        padding :0;
        height : 93vh;
    }
    .myOrdersHeading{
        font : 400 2.2vmax "Roboto";
        padding : 4vw;
    }
    .myOrdersTable div{
        font : 300 4vw "Roboto";
    }
    .MuiDataGrid-columnHeader{
        padding : 20px !important;
    }
    .MuiDataGrid-columnHeader div {
        font : 500 5vw "Roboto" !important;
    }
}