@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@200&family=Roboto:ital@1&family=Source+Sans+Pro:wght@200&display=swap");
@import url("./components/Card/Comment_css.css");
@import url("./components/Cart/Cart.css");
@import url("./components/Shipping/Shipping.css");
@import url("./components/CartItemCard/CartItemCard.css");
@import url("./components/User/User.css");
@import url("./components/ConfirmOrder/ConfirmOrder.css");
@import url("./components/Payment/Payment.css");
@import url("./components/OrderSuccess/OrderSuccess.css");
@import url("./components/MyOrders/MyOrders.css");
@import url("./components/Home/Home.css");
@import url('./components/OderDetails/OrderDetails.css');
@import url('./components/User/Profile.css');
@import url('./components/User/UpdateProfile/UpdateProfile.css');
* {
  margin: 0;
  scroll-behavior: smooth;
}
.search-container {
  display: flex;
  flex-direction: column;
  width: 100vmax;
  box-sizing: border-box;
}
.search {
  position: absolute;
  line-height: 0;
  top : 1%;
  left : 2%;
  display: flex;
  flex : 1;
  flex-direction: row;
  margin: 0px;
}
.searchformInput {
  position: relative;
  background: rgba(244, 244, 244, 0.6);
  font-size: 14px;
  color: #909090;
  height: 37px;
  border: none;
  width: 300px;
  font-weight: 500;
  padding: 0 0px 0 32px;
  /* box-shadow: 5px 3px 8px #afc8cf; */
  outline: none;
  text-overflow: ellipsis;
  border-radius: 2px 0px 0px 2px;
  font-family: "font-regular";
}

.search-btn {
  height: 37px;
  font-size: 14px;
  background-color: #daffef;
  color: black;
  cursor: pointer;
  margin: 0px;
  width: 50px;
  border: none;
}
i {
  margin-right: 3px;
  color: black;
}
.search-hidden {
  /* box-shadow: 0px 1px 18px #888888; */
  position: absolute;
  z-index: 9;
  width: 300px;
  background-color: #fffbff;

  display: flex;
  flex-direction: column;
  top: 41px;
  left : 2%;
  overflow: hidden;
  overflow-y: auto;
  box-shadow: 0 4px 12px 0 rgb(0 0 0 / 5%);
}

.search-hidden::-webkit-scrollbar {
  display: none;
}
.search input:focus {
  outline: none;
  border-left: 1px solid black;
  /* box-shadow: 5px 0px 8px #888888; */
}
.search-item {
  color: black;
  width: 100%;
  font-weight: 400;
  font-size: 14px;
  padding-left: 33px;
  padding-top: 3px;
  height: 20px;
  align-items: left;
  text-decoration: none;
}
.search-item:hover {
  background-color: #e8ebe4;
}

.ProductDetails {
  width: 80vw;
  max-width: 100%;
  background-color: rgb(250, 250, 250);
  margin: 5% 10%;
  box-shadow: 2px 2px gray;
  box-sizing: border-box;
  display: flex;
}
.ProductDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  height: 50%;
  border: 1px solid white;
}
.ProductDetails > div:last-child {
  align-items: flex-start;
}

.carouselImage {
  width: 20vmax;
}
.carousel {
  margin-left: 10%;
}

.B1 > p {
  color: rgba(54, 54, 54, 0.582);
  font: 200 0.6vmax cursive;
}
.B1 > h2 {
  color: rgb(54, 54, 54);
  font: 600 1.6vmax;
}
.B2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.285);
  border-bottom: 1px solid rgba(0, 0, 0, 0.285);
  width: 70%;
  padding: 1vmax 0;
  margin-bottom: 20px;
}
.B3 {
  width: 70%;
}
.B3 > h2 {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.8vmax;
  margin: 1vmax 0;
}
.B3-a {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.B3-b {
  width: 44%;
}

.status {
  margin-bottom: 10px;
  color: rgb(27, 25, 25);
  font-weight: bold;
  font-family: "Source Sans Pro", sans-serif;
}
.status > span {
  font-weight: light;
}
.redColor {
  color: red !important;
}
.greenColor {
  color: green !important;
}
.btn {
  background-color: #73bbc4;
  border: 1px solid #f8f9fa;
  border-radius: 4px;
  color: #3c4043;
  cursor: pointer;
  font-family: arial, sans-serif;
  font-size: 14px;
  height: 26px;
  line-height: 27px;
  min-width: 54px;
  padding: 0 16px;
  margin-right: 2px;
  text-align: center;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: pre;
}
.btn:hover {
  border-color: #dadce0;
  box-shadow: rgba(0, 0, 0, 0.1) 0 1px 1px;
  color: #202124;
}

.btn:focus {
  border-color: #4285f4;
  outline: none;
}
.npi {
  padding: 3px;
  margin: 0px 3px;
  border: 0;
  width: 15%;
  border-bottom: 2px solid rgb(27, 26, 26);
}

.btn-1 {
  background-color: #0095ff;
  border: 1px solid transparent;
  border-radius: 3px;
  box-shadow: rgba(255, 255, 255, 0.4) 0 1px 0 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system, system-ui, "Segoe UI", "Liberation Sans",
    sans-serif;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.15385;
  margin: 0 2px;
  outline: none;
  padding: 8px 0.8em;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  white-space: nowrap;
}

.btn-1:hover,
.button-7:focus {
  background-color: #07c;
}

.btn-1:focus {
  box-shadow: 0 0 0 4px rgba(0, 149, 255, 0.15);
}

.btn-1:active {
  background-color: #0064bd;
  box-shadow: none;
}
.d-heading {
  font-size: 20px;
  font-weight: 550;
  color: rgb(30, 28, 28);
  background-clip: text;
  padding-bottom: 0px;
  margin: 0px;
  -webkit-background-clip: text;
}
.p-description {
  opacity: 0.8;
  margin-bottom: 50px;
}

@media screen and (max-width: 770px) {
  .B3-a {
    flex-direction: column;
    align-items: flex-start;
  }
  .cart-adding {
    margin-top: 10%;
  }
  .B3-b {
    width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .ProductDetails {
    flex-direction: column;
    height: unset;
  }
  .ProductDetails > div:last-child {
    align-items: center;
  }
  B1 > h2 {
    font-size: 2.8 vmax;
    text-align: center;
  }
  B1 > p {
    text-align: center;
    font-size: 1vmax;
  }
  .B2 {
    justify-content: center;
  }
  .B2 > span {
    font-size: 1.5vmax;
  }
  .proid {
    font-size: 1em !important;
  }
  .btn {
    height: 3em;
    width: 10em;
  }
  .carouselImage {
    height: 50vmax;
  }
}

.comments-heading {
  box-shadow: 1px 1px 1px 1px lightblue;
  padding: 3px;
  margin-bottom: 2px;
  text-align : center;
    font-family: "Roboto";
    font-size : 1.4vmax;
    border-bottom : 1px solid black;
    width : 20vmax;
    padding : 1vmax;
    margin : 5vmax auto;
    color : black;
}

.comment-input {
  background-color: transparent;
  border-radius: 0;
  border: none;
  margin-left: 20px;
  border-bottom: 1px solid blue;
  font-family: inherit;
  font-size: inherit;
}
.comment-input:focus {
  outline: none;
}
.product-section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 4%;
}
.product-section-item {
  margin: 20px;
  padding: 30px;
}

.cat-product {
  display: flex;
  flex-direction: row;
}
.cat-item {
  list-style: none;
  transition: all 0.1s;
}
.cat-item:hover {
  opacity: 0.5;
  color: lightcoral;
  cursor: pointer;
}

.cat-price-likes-heading {
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  margin-bottom: 0.5em;
  font-family: "Source Sans Pro", sans-serif;
}
.cat-price {
  display: "flex";
  flex-direction: column;
  justify-content: space-between;
  margin-top: 10%;
}
.text-comment {
  width: 100%;
  height: 80px;
  padding: 5px 10px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  font-size: 16px;
}
.likes-btn {
  box-sizing: border-box;
  color: #3b3b3b;
  cursor: pointer;
  display: inline-block;
  font-weight: 600;
  line-height: normal;
  margin-top: 10px;
  min-width: 0;
  padding: 2px 2px;
  text-align: center;
  transition: all 0.1ms cubic-bezier(0.23, 1, 0.32, 1);
  width: 2%;
  will-change: transform;
}

.likes-btn:disabled {
  pointer-events: none;
}

.likes-btn:hover {
  color: rgb(195, 179, 179);
  transform: translateY(-2px);
}

.likes-btn:active {
  box-shadow: none;
  transform: translateY(0);
}

.price-btn {
  align-items: center;
  appearance: none;
  background-color: #fcfcfd;
  border-radius: 4px;
  border-width: 0;
  box-sizing: border-box;
  color: #36395a;
  cursor: pointer;
  display: inline-flex;
  font-size: 1rem;
  font-family: "JetBrains Mono", monospace;
  height: 20px;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow 0.15s, transform 0.15s;
  user-select: none;
  -webkit-user-select: none;
  width: 10%;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
}

.price-btn:focus {
  box-shadow: #d6d6e7 0 0 0 1.5px inset, rgba(45, 35, 66, 0.4) 0 2px 4px,
    rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #d6d6e7 0 -3px 0 inset;
}

.price-btn:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px,
    rgba(45, 35, 66, 0.3) 0 5px 5px -3px, #d6d6e7 0 -1px 0 inset;
  transform: translateY(-2px);
}

.price-btn:active {
  box-shadow: #d6d6e7 0 3px 7px inset;
  transform: translateY(2px);
}
.price-input {
  font-size: 16px;
  font-size: max(16px, 1em);
  font-family: inherit;
  padding: 0.05em 0.1em;
  background-color: #fff;
  width: 30%;
  border: 0px;
  border-left: 1px solid #00f;
  border-right: 1px solid #00f;
  border-bottom: 1px solid #00f;
}
.price-input:focus {
  outline: none;
}
.slidecontainer {
  width: 100%;
}

.slider {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 15px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

/* Mouse-over effects */
.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  background: #04aa6d;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 15px;
  height: 15px;
  background: #04aa6d;
  cursor: pointer;
}
@media screen and (max-width: 752px) {
  .cat-product {
    flex-direction: column;
  }
  .cat-price-likes-heading {
    width: 50%;
  }
  .cat-price {
    border-bottom: 1px solid black;
    margin: 12vmax 5vmax;
  }
  hr {
    width: 50%;
  }
  .price-input {
    width: 21%;
  }
  price-btn {
    width: 10%;
  }
}
