.footer-container {
    width: 100%;
    max-width: 100%;
    display : grid;
    grid-template-columns: 7fr 1fr;
    color : white;
    background-color : black;
    height: 100%;
    max-height: 100%;
    padding : 5rem;
    align-items: start;
    grid-auto-rows: 20px;
    justify-content: center;
    align-content: center;
}

.logo {
    font : 500 3rem cursive;
    color : red;
}

.slogan {
    font : 500 1rem sans-serif;
}

.second-piece {
    margin-top : 20px;
    display : flex;
    flex-direction: column;
}
.second-piece a{
    text-decoration : none;
    color : purple
}

.copyright{
    position : relative;
    bottom :0px;
    left : 50%;
    width: 200px;
    min-width: 20px;
}

@media screen and (max-width:784px){
    .footer-container{
        display: flex;
        flex-direction: column;
    }
}