.shippingContainer{
    display : flex;
    width : 100vw;
    max-width: 100%;
    justify-content : center;
    align-items : center;
    flex-direction : column;
}

.shippingBox {
    background-color : rgb(243, 239, 239);
    width : 25vw;
    height : 90vh;
    box-sizing : border-box;
    overflow : hidden;
}

.shippingHeading{
    text-align : center;
    color : rgba(0,0,0,0.664);
    font : 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom : 1px solid rgba(0,0,0,0.205);
    width : 50%;
    margin : auto;
}

.shippingForm{
    display : flex;
    flex-direction : column;
    align-items : center;
    margin : auto;
    padding : 2vmax;
    justify-content : space-evenly;
    height: 80%;
    transition : all 0.5s;
}

.shippingForm > div{
    display : flex;
    width : 100%;
    align-items : center;
}

.shippingForm > div > input, 
.shippingForm > div > select{
    padding : 1vmax 4vmax;
    padding-right : 1vmax;
    width : 100%;
    box-sizing : border-box;
    border : 1px solid rgba(0,0,0,0.267);
    border-radius : 4px;
    font : 300 0.9vmax cursive;
    outline : none;
}

.shippingForm > div > .icon{
    position : absolute;
    transform : translateX(1vmax);
    font-size : 1.6vmax;
    color : rgba(181, 86, 86, 0.623);
}
.shippingBtn {
    border : none;
    background-color : blue;
    color : white;
    cursor : pointer;
    transition : all 0.5s;
    outline : none;
    margin : 2vmax;
    width : 100%;
    height: 90%;
    font: 300 1.3vmax "Roboto";
}

.shippingBtn:hover {
    background-color : lightblue;
}

@media screen and (max-width: 600px) {
    .shippingBox {
        width : 100vw;
        height : 95vh;
    }
    .shippingHeading{
        font : 400 6vw "Roboto";
        padding : 5vw;
    }
    .shippingForm {
        padding : 11vw;
    }
    .shippingForm > div > input,
    .shippingForm > div > select
    {
        padding : 5vw 10vw;
        font : 300 4vw cursive;
    }
    .shippingForm > div > svg {
        font-size  : 6vw;
        transform : translateX(3vw);
    }
    .shippingBtn{
        font : 300 4vw "Roboto";
        padding : 4vw;
    }
}