.sidebar{
    background-color : white;
    padding : 4rem 0;
    display: flex;flex-direction: column;
}

.sidebar > a:first-child{
    padding : 0;
}

.sidebar a {
    text-decoration : none;
    color : black;
    font : 200 1rem "Roboto";
    padding : 2.1rem;
    transition : all 0.5s;
}

.sidebar a:hover{
    color : blue;
    transform : scale(1.1);
}

.sidebar a > p{
    display : flex;
    align-items : center;
}

.sidebar a > p > svg {
    margin-right : 0.5rem;
}

.MuiTypography-root  {
    background-color : white !important;
}