@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu',sans-serif;
  background-color: #fdfdfd;
}


.navbar {
  background-color: #161615;
  position: relative;
  height : 49px;
  position: sticky;
  top : 0;
  z-index: 10;
}

.menu-icon {
  display: none;
}

.nav-elements ul {
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
}

.nav-elements ul li:not(:last-child) {
  margin-right: 60px;
}
.nav-elements ul li{
  margin-top : 0.8%;
}

.nav-elements ul a {
  font-size: 16px;
  font-weight: 400;
  color: #f9efef;
  text-decoration: none;
}
.nav-elements ul a:hover{
  color : grey;
}

@media (max-width: 768px) {
  .nav-elements ul li:not(:last-child) {
    margin-right: 30px;
  }
}

@media (max-width: 600px) {
  .menu-icon {
    display: block;
    cursor: pointer;
    position: absolute;
    right : 0;
    margin-top : 8px;
  }

  .nav-elements {
    position: absolute;
    right: 0;
    top: 40px;
    background-color: #0a0a0a;
    opacity: 0.8;
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
  }

  .nav-elements.active {
    width: 150px;
  }

  .nav-elements ul {
    display: flex;
    flex-direction: column;
  }

  .nav-elements ul li {
    margin-right: unset;
    margin-top: 22px;
  }
}

.user-profile {
  width : 25%;
  min-width: 25%;
  margin : 0;
  border-radius: 50%;
}


.fa-search{
  transition: 1s;
  cursor: pointer;
  margin-left: 30px;
}


input[type=name], input[type=email], input[type=password], input[type=number],
.container input[type=text]{
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: vertical;
  margin-bottom : 1%;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}
select,textarea{
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: vertical;
  font-family: "Roboto";
  margin-bottom : 1%;
}
#createProductFormImage > img{
  width : 5vmax;
  margin : 1px 0.1vmax;
}
#createProductFormImage{
  width : 100%;
  height : 50px;
  overflow: auto;
}
label {
  padding: 10px 10px 10px 10px;
  display: inline-block;
}

.btn-form {
  background-color: #678bce;
  color: white;
  margin-top: 20px;
  margin-right : 4%;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: right;
}

.btn-form:hover {
  background-color: #8e9cd5;
  transition: all 0.5s;
}

.container {
  border-radius: 5px;
  background-color: white;
  margin: auto;
  padding-bottom: 20px;
  width : 40%;
  -moz-box-shadow: 0 0 3px rgb(234, 224, 224);
-webkit-box-shadow: 0 0 3px rgb(234, 228, 228);
box-shadow: 0 0 3px rgb(225, 222, 222);
}
.super-container{
  min-height:100vh;
  margin : 0;
  padding : 58px;
  min-width: 100vw;
  background-color: rgb(241, 233, 233);
}
.container h1 {
  margin : 0;
  margin-bottom: 5px;
  background-color: rgb(239, 235, 154)
}
.col-25 {
  float: left;
  width: 25%;
  margin-top: 6px;
}

.col-75 {
  padding-right : 20px;
  float: left;
  width: 75%;
  margin-top: 6px;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

@media screen and (max-width: 965px) {
  .container{
    padding : 5px;
    width: 80%;
    padding-top : 0;
  }
  .col-25, .col-75{
    width: 100%;
    padding: 0;
    margin-top: 5px;
  }
  .btn-form{
    width: 20%;
    margin-top: 5px;
    margin-right: 0px;
  }
}
@media screen and (max-width: 595px) {
  .container{
    width: 100%;
  }
  .btn-form{
    width: 100%;
  }
}
@media screen and (max-width: 1130px) {
  .extra{
    padding: 20px;
    width: 70%;
  }
  .col-extra-25, .col-extra-75{
    width: 100%;
    margin-top: 5px;
  }
  #createProductFormImage > img{
    height : 70px;
  }
  #createProductFormImage{
    height : 80px;
  }
}

.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
  position: absolute;
  visibility: hidden;
  width: 50px;
  background-color: black;
  color: #fff;
  font-size:  12px;
  top : 120%;
  text-align: center;
  padding: 1px 0;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}

.speedDial {
  position : fixed;
  right : 0.6vmax;
  top : 4vmax;
}

.speedDialIcon {
  width : 56px;
  height : 56px;
  border-radius : 100%;
}
