.banner {
  background-image: linear-gradient(315deg, #041e6b 0%, #05519d 74%);
  height: 100vmin;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
}
.banner p {
  font: 3.4vmax "Source Sans Pro";
  color: black;
}
.banner:after {
  content: "";
  width: 100vw;
  height: 100vmin;
  background-color: white;
  position: absolute;
  top: 0%;
  left: 0;
  max-width: 100%;
  -webkit-clip-path: polygon(
    49% 91%,
    100% 82%,
    100% 100%,
    50% 100%,
    0 100%,
    0 83%
  );
  clip-path: polygon(49% 91%, 100% 82%, 100% 100%, 50% 100%, 0 100%, 0 83%);
}
.heading {
  text-align: center;
  font-family: "Roboto";
  font-size: 1.4vmax;
  border-bottom: 1px solid black;
  width: 20vmax;
  padding: 1vmax;
  margin: 5vmax auto;
  color: black;
}

.product-card {
  display: flex;
  background-color: white;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 10vmax;
  margin-left: 12.5vmax;
  margin-right: 12vmax;
}

.each-product-card {
  cursor: pointer;
  margin: 3vmax;
  transition: all 0.5s ease;
}
.each-product-card:hover {
  transform: translateY(-1vmax);
}
