.CartItemCard{
    display : flex;
    padding : 1vmax;
    height : 8vmax;
    align-items : 'flex-start';
    box-sizing : border-box;
}
.CartItemCard > img{
    width : 5vmax;
}
.CartItemCard > div{
    display : flex;
    margin : 0.6vmax 1vmax;
    flex-direction : column;
}
.CartItemCard > div > a{
    font : 300 0.9vmax cursive;
    color : rgba(24,24,24,0.815);
    text-decoration: none;
}
.CartItemCard > div  span{
    font : 300 0.9vmax "Roboto";
    color : rgba(24,24,0.815);
}
.CartItemCard > div > a{
    color : orange;
    font: 100 0.8vmax "Roboto";
    cursor : pointer;
}
.CartItemCard > div > p{
    font : 100 0.8vmax "Roboto";
}
.CartItemCard .carditem-link:hover{
    box-shadow: white 0 2px 4px, rgba(45, 35, 66, 0.3) 0 5px 5px -3px, white 0 -1px 0 inset;
  transform: translateY(-2px);
  cursor : pointer;
}

@media screen and (max-width : 600px){
    .CartItemCard {
        padding : 3vmax;
        height : 24vmax;
    }
    .CartItemCard > div{
        margin : 1vmax 2vmax;
    }
    .cartItemCard > div > a{
        font : 300 2vmax cursive;
    }
    .cartItemCard > div > span {
        font : 300 1.9vmax "Roboto";
    }
    .cartItemCard > div > p{
        font : 100 1.3vmax "Roboto";
    }
}